<template>
  <button @click="goBack" class="back-button">
    <span class="icon">←</span>
    <span class="text">返回</span>
  </button>
</template>

<script>
export default {
  methods: {
    goBack() {
      this.$router.go(-1); // 使用 Vue Router 的 go(-1) 方法返回上一页
    }
  }
}
</script>

<style scoped>
.back-button {
  position: fixed;
  bottom: 35px; /* 距离底部 20px */
  left: 50%;
  transform: translateX(-50%); /* 水平居中对齐 */
  width: 50px; /* 按钮宽度 */
  height: 50px; /* 按钮高度 */
  background-color: rgba(0, 0, 0, 0.7); /* 半透明绿色背景 */
  color: white;
  border: none;
  border-radius: 50%; /* 圆形按钮 */
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3); /* 添加阴影 */
  font-size: 24px; /* 图标大小 */
}

.back-button .icon {
  font-size: 24px; /* 图标字体大小 */
}

.back-button .text {
  display: none; /* 隐藏文字，仅显示图标 */
}

/* 添加媒体查询以适应小屏幕 */
@media (min-width: 768px) {
  .back-button {
    width: 60px;
    height: 60px;
  }

  .back-button .icon {
    font-size: 28px; /* 图标在大屏幕上的大小 */
  }
}
</style>
